@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home-header {
  background-image: url(/static/media/fundo.3f85c7e0.jpeg);
  min-height: 100vh;
  background-position: right center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.6);
  background-blend-mode: overlay; 
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 15px 0;
}

.home-header h1 {
  font-size: 3rem;
  color: white;
}
.cadastro {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.pagina {
  width: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pagina h2 {
  margin-top: 90px;
}

.pagina__botao {
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.pagina__botao > button {
  border: none;
  background: none;
  font-size: 18px;
  font-weight: bold;
  color: #242038;
  cursor: pointer;
}

.botao-imagem{
  display: block;
  width: 150px;
  margin-bottom: 10px;
}

.botao-icone{
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.formulario {
  width: 100%;
}

.botao {
  background: -webkit-linear-gradient(270.76deg, #834FC6 -27.71%, #F7D9BB -12.76%, #834FC6 78.2%);
  background: linear-gradient(179.24deg, #834FC6 -27.71%, #F7D9BB -12.76%, #834FC6 78.2%);
  border-radius: 3px;
  color: #242038;
  font-weight: bold;
  font-size: 18px;
  padding: 5px 15px;
  border: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.botao--desabilitado {
  color: gray;
  background: lightgrey;
  pointer-events: none;
}

.campo {
  font-size: 1rem;
  box-sizing: border-box;
  display: block;
  width: 100%;
  padding: 0.8rem;
  margin: 0 0 1rem;
  border: 1px solid #242038;
  border-radius: 3px;
}

.campo:focus {
  outline: none;
  border-color: #834FC6 ;
  box-shadow: 0 0 10px #9351b4;
}

.grupo__erro {
  color: red;
  font-size: small;
}

form {
  width: 100%;
}
.comentario {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  border-radius: 10px;
  background-color: #f3f3f3;
  padding: 25px;
  display: flex;
  align-items: center;
  max-width: 960px;
  margin: 50px auto;
}

.comentario__perfil {
  width: 100%;
  max-width: 200px;
  margin-right: 50px;
  border-radius: 10px;
}

.comentario__nome {
  margin: 0;
  font-size: 24px;
}

.comentario__subtitulo {
  margin: 0;
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 20px;
}

.comentario hr {
  max-width: 200px;
  margin-left: 0;
}
.chat {
  padding: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.chat>span{
  margin-top:50px;
  font-size: 30px;
  font-weight: bold;
}
.navbar-menu__botao {
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 0.5rem;
  border: 1px solid #ffffff;
  border-radius: 5px;
}

.navbar-menu__botao--aberto:after {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  background: rgba(0, 0, 0, 0.5);
}

.navbar-menu__opcoes {
  list-style: none;
  color: white;
  text-transform: uppercase;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 80vw;
  padding: 2rem 1rem 0;
  margin: 0;
  background: #242038;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  -webkit-transition: -webkit-transform 1s ease-in-out;
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.navbar-menu__opcoes--aberto {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.navbar-menu__opcoes li {
  cursor: pointer;
}

.navbar-menu__opcoes li + li {
  margin-top: 1rem;
}

@media (min-width: 992px) {
  .navbar-menu__botao {
    display: none;
  }

  .navbar-menu__opcoes {
    display: flex;
    align-items: center;
    position: static;
    position: initial;
    height: auto;
    height: initial;
    width: auto;
    width: initial;
    padding: 0;
    padding: initial;
    background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
    background: initial;
    -webkit-transform: none;
            transform: none;
    -webkit-transform: initial;
            transform: initial;
    -webkit-transition: none 0s ease 0s;
    transition: none 0s ease 0s;
    -webkit-transition: initial;
    transition: initial;
  }

  .navbar-menu__opcoes--aberto {
    -webkit-transform: none;
            transform: none;
    -webkit-transform: initial;
            transform: initial;
  }

  .navbar-menu__opcoes--ativo {
    border-bottom: 2px solid #ffffff;
  }

  .navbar-menu__opcoes li + li {
    margin-top: 0;
    margin-left: 1rem;
  }

  .navbar-menu__opcoes li a {
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    padding: 0.3rem 0;
  }

  .navbar-menu__opcoes li a:hover {
    color: #834FC6;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }
  
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 0 1 60px;
  min-height: 90px;
  padding: 0 5%;
  color: #ffffff;
  background-color: #242038;
  position: fixed; 
  top: 0; 
  width: 100%; 
  z-index: 99;
}

.navbar-logo {
  display: block;
  width: 200px;
}

.navbar-links {
  list-style: none;
  display: flex;
  align-items: center;
  padding: 0;
}

.navbar-links li {
  margin-left: 1rem;
}

.navbar-links a {
  color: #ffffff;
  text-transform: uppercase;
  padding-bottom: 0.3rem;
}

.navbar-links__ativo {
  border-bottom: 2px solid #ffffff;
}
